.button {
  box-sizing: border-box;
  padding: 30px;
  color: #ffffff;
  text-align: center;
  outline: none;
  background-color: #D30531;
  text-transform: none;
  border-radius: 8px;
  display: block;
  font-size: 16px;
  border: none;
  cursor: pointer;
}
.page-header {
  color: #000000;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  position: fixed;
  top: 0;
  background-color: #ffffff;
  flex-direction: row;
  box-sizing: border-box;
  margin: 9px auto 0;
  padding: 0 8px;
  width: 100%;
  z-index: 7;
}
@media (min-width: 800px) {
  .page-header {
    margin-top: 0;
    padding: 0 34px;
  }
}
.page-header__logo {
  margin-right: 75px;
  width: 195px;
  height: 30px;
  padding-left: 8px;
}
@media (min-width: 800px) {
  .page-header__logo {
    width: 295px;
    padding-left: 53px;
    height: 44px;
    padding-top: 11px;
  }
}
.main-nav {
  position: relative;
  width: 30px;
  height: 30px;
}
@media (min-width: 800px) {
  .main-nav {
    order: -1;
  }
}
.main-nav__toggle {
  position: absolute;
  display: block;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  top: 7px;
  left: -3px;
  cursor: pointer;
  z-index: 3;
  box-sizing: border-box;
  padding: 0;
}
.main-nav__toggle:focus,
.main-nav__toggle:hover {
  opacity: 0.5;
}
.main-nav__toggle:active {
  opacity: 0.2;
}
.main-nav--closed .main-nav__toggle {
  width: 22.5px;
  height: 17.25px;
  top: 7px;
  left: -5px;
  background-image: url("../img/gamburger.svg");
}
@media (min-width: 800px) {
  .main-nav--closed .main-nav__toggle {
    width: 30px;
    height: 23px;
    top: -2px;
    left: 0;
  }
}
.main-nav--opened .main-nav__toggle {
  background-image: url("../img/icon-close.svg");
  width: 20px;
  height: 30px;
}
@media (min-width: 800px) {
  .main-nav--opened .main-nav__toggle {
    width: 27px;
    height: 30px;
    top: -4px;
    left: 2px;
  }
}
.main-nav__wrapper {
  width: 268px;
  box-shadow: 0 3px 12px #00000029;
}
@media (min-width: 800px) {
  .main-nav__wrapper {
    width: 280px;
  }
}
.main-nav__list {
  position: fixed;
  top: 48px;
  right: -4px;
  list-style: none;
  margin: 0;
  padding: 0 20px;
  background-color: #ffffff;
  opacity: 0.95;
  width: 234px;
  min-height: 721px;
  z-index: 5;
}
@media (min-width: 800px) {
  .main-nav__list {
    left: 1px;
    width: 240px;
    margin-top: 21px;
  }
}
.main-nav__item {
  margin-top: 21px;
}
.main-nav__item--process {
  margin-top: 38px;
}
.main-nav__item-title {
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  text-transform: uppercase;
  line-height: 22px;
}
.main-nav__link {
  font-weight: 600;
  text-transform: none;
  color: #004685;
  text-decoration: underline;
  margin-top: 18px;
  letter-spacing: -0.4px;
  cursor: pointer;
}
.main-nav__link:focus,
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__points-list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: -7px;
  letter-spacing: -0.3px;
}
@media (min-width: 800px) {
  .main-nav__points-list {
    margin-top: 0;
  }
}
.main-nav__points-link {
  font-weight: 600;
  text-transform: none;
  color: #004685;
  text-decoration: underline;
  margin-top: 22px;
}
@media (min-width: 800px) {
  .main-nav__points-link {
    margin-top: 19px;
    line-height: 22px;
    letter-spacing: -0.4px;
  }
}
.main-nav__points-item:nth-child(2) .main-nav__points-link {
  margin-top: 9px;
}
@media (min-width: 800px) {
  .main-nav__points-item:nth-child(2) .main-nav__points-link {
    margin-top: 22px;
    margin-bottom: 22px;
  }
}
.main-nav__points-item:last-child .main-nav__points-link {
  margin-top: 19px;
}
@media (min-width: 800px) {
  .main-nav__points-item:last-child .main-nav__points-link {
    margin-top: 22px;
  }
}
.main-nav__points-link:focus,
.main-nav__points-link:hover {
  opacity: 0.6;
}
.main-nav__points-link:active {
  opacity: 0.3;
}
.main-nav--closed .main-nav__list {
  display: none;
}
.main-nav--nojs .main-nav__list {
  display: block;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.page-header__phone {
  display: none;
}
@media (min-width: 800px) {
  .page-header__phone {
    display: block;
    position: relative;
    width: 43px;
  }
}
@media (min-width: 800px) {
  .page-header__label {
    position: absolute;
    box-sizing: border-box;
    width: 43px;
    height: 43px;
    top: -43px;
    left: 7px;
    background-image: url("../img/ic_help.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    cursor: pointer;
  }
  .page-header__label:focus,
  .page-header__label:hover {
    opacity: 0.7;
  }
  .page-header__label:active {
    opacity: 0.4;
  }
}
.page-header__phone-content {
  display: none;
}
@media (min-width: 800px) {
  .page-header__phone-content {
    position: absolute;
    width: 241px;
    height: 150px;
    top: 42px;
    right: -16px;
    z-index: 10;
    background-color: #ffffff;
    box-shadow: 0 3px 12px #00000033;
  }
  .page-header__phone-content::after {
    position: absolute;
    content: "";
    width: 15px;
    height: 15px;
    top: -8px;
    right: 18px;
    transform: rotate(45deg);
    background-color: #ffffff;
  }
}
.helpdesk-title {
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  margin: 0;
  margin-top: 22px;
}
.helpdesk-tel {
  font-size: 28px;
  font-weight: 600;
  text-align: center;
  margin: 0;
  margin-top: -2px;
}
.helpdesk-mail {
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  margin-top: 20px;
  margin-left: -2px;
  color: #004685;
  position: relative;
}
.helpdesk-mail::after {
  content: "Написать письмо";
  position: absolute;
  border-bottom: 1px solid #004685;
  top: 0;
  right: 48px;
  line-height: 15px;
  box-sizing: border-box;
}
.helpdesk-mail:hover::after,
.helpdesk-mail:focus::after {
  opacity: 0.7;
  content: "Служба поддержки";
  right: 30px;
  transition: all 0.3s;
}
.page-header__input:checked ~ .page-header__phone-content {
  display: block;
}
.page-header__input:focus ~ .page-header__label {
  border: 1px solid #004685;
}
.page-title {
  color: #ffffff;
  margin-top: 48px;
  background-color: #00468533;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("../img/pic_main_mobile.png");
  min-height: 215px;
}
@media (min-resolution: 2dppx) {
  .page-title {
    background-image: url("../img/pic_main@2x.png");
  }
}
@media (min-width: 800px) {
  .page-title {
    margin-top: 68px;
    min-height: 360px;
    background-image: url("../img/pic_main.png");
  }
}
@media (min-width: 800px) and (min-resolution: 2dppx) {
  .page-title {
    background-image: url("../img/pic_main@2x.png");
  }
}
.page-title__title {
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  margin: 0;
  padding-top: 39px;
  padding-left: 0;
}
@media (min-width: 800px) {
  .page-title__title {
    text-align: left;
    font-size: 36px;
    padding-top: 87px;
    padding-left: 2px;
  }
}
.page-title__form {
  margin-top: 17px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  overflow: hidden;
}
@media (min-width: 800px) {
  .page-title__form {
    margin: 0;
    padding: 0;
    margin-top: 12px;
    margin-left: -2px;
  }
}
.page-title__form-input {
  width: 100%;
  padding: 13px 9px;
  border: 2px solid #00468533;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px;
  color: #000000;
  cursor: pointer;
  margin-left: 11px;
  margin-right: 7px;
}
@media (min-width: 800px) {
  .page-title__form-input {
    width: 800px;
    height: 60px;
    margin-left: 3px;
    margin-right: 0;
    padding: 22px;
  }
}
.page-title__form-button {
  position: relative;
  font-size: 24px;
  font-weight: 600;
  width: 0;
  height: 0;
  padding: 0;
  margin: 0;
  clip: rect(0 0 0 0);
}
@media (min-width: 800px) {
  .page-title__form-button {
    width: 142px;
    height: 60px;
    opacity: 0.9;
  }
}
.page-title__form-button::before {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("../img/ic_search.svg");
  width: 25px;
  height: 25px;
  right: 21px;
  top: 12px;
  z-index: 2;
  cursor: pointer;
}
@media (min-width: 800px) {
  .page-title__form-button::before {
    display: none;
  }
}
.page-title__form-button:focus,
.page-title__form-button:hover,
.page-title__form-button:focus::before,
.page-title__form-button:hover::before {
  opacity: 0.7;
}
@media (min-width: 800px) {
  .page-title__form-button:focus,
  .page-title__form-button:hover,
  .page-title__form-button:focus::before,
  .page-title__form-button:hover::before {
    opacity: 1;
  }
}
.page-title__form-button:active,
.page-title__form-button:active::before {
  opacity: 0.4;
}
@media (min-width: 800px) {
  .page-title__form-button:active,
  .page-title__form-button:active::before {
    opacity: 0.6;
  }
}
.page-title__description {
  font-size: 12px;
  font-weight: 400;
  padding-left: 10px;
  margin-top: 9px;
  line-height: 16px;
}
@media (min-width: 800px) {
  .page-title__description {
    margin-top: 12px;
