  color: #004685;
  text-decoration: underline;
  display: inline-block;
}
.rosreestr-link:hover,
.rosreestr-linkfocus {
  opacity: 0.7;
}
.rosreestr-link:active {
  opacity: 0.5;
}
@media (max-width: 799px) {
  .services__input:checked ~ .item-content {
    display: block;
  }
  .services__item:focus .services__label,
  .services__item:hover .services__label {
    opacity: 0.7;
  }
  .services__item:active .services__label {
    opacity: 0.5;
  }
}
.services__label::after {
  position: absolute;
  right: 6px;
  top: -11px;
  display: block;
  transition: all 0.3s;
  content: url("../img/ic_dropdown-arrow.svg");
  z-index: 3;
}
@media (min-width: 800px) {
  .services__label::after {
    display: none;
  }
}
.services__label::before {
  position: absolute;
  left: -2px;
  top: -18px;
  display: block;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto;
  width: 55px;
  height: 55px;
  z-index: 3;
}
@media (min-width: 800px) {
  .services__label::before {
    display: none;
  }
}
.services__label--penalties::before {
  background-image: url("../img/ic_gibdd1.svg");
}
.services__label--legal-debts::before {
  background-image: url("../img/ic_fssp.svg");
}
.services__label--taxes::before {
  background-image: url("../img/ic_nalog.svg");
}
.services__label--rosreestr::before {
  background-image: url("../img/ic_rr.svg");
}
.services__input:checked + .services__label::after {
  transform: rotate(180deg);
  top: -14px;
}
