.popup {
  display: none;
  height: 500px;
  width: 366px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: auto;
  background-color: #ffffff;
  border: 1px solid #00000075;
  text-align: left;
  overflow: hidden;
  box-sizing: border-box;
  z-index: 15;
  box-shadow: 0 0 0 1000vw #323232;
}
@media (min-width: 800px) {
  .popup {
    width: 400px;
    height: 469px;
  }
}
.popup__wrapper {
  margin: 0 auto;
  position: relative;
  width: 320px;
}
.close-button {
  position: absolute;
  top: 14px;
  right: -7px;
  width: 22px;
  height: 20px;
  cursor: pointer;
}
@media (min-width: 800px) {
  .close-button {
